export default class BrowserUtil {

  //zhen
  static browserLang() {
    let lang = navigator.language || navigator.userLanguage;//IE
    lang = lang.substr(0, 2);//lang2
    if (lang === "zh") {
      return "zh"
    } else {
      return "en"
    }
  }

}



